.nav {
  padding: 30px 88px;
  justify-content: space-between;
  @include flex-center;
  @include pad {
    padding: 30px 40px;
  }
  @include mobile {
    padding: 10px 0 0 0;
    flex-wrap: wrap;
    justify-content: center;
  }
}

.nav-logo {
  background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/logo.png");
  width: 80px;
  height: 49px;
  @include bg-img;
  @include hide-text;
  background-size: contain;
  @include mobile {
    margin: 0 auto 10px auto;
    width: 48px;
    height: 30px;
  }
}

.nav-list {
  @include flex-gap(64px);
  @include list-hover;
  @include pad {
    gap: 40px;
  }
  @include mobile {
    flex-wrap: wrap;
    gap: 0;
    width: 100%;
    li {
      width: 50%;
      font-size: font(1.25);
      padding: 8px 0;
      text-align: center;
      border: 1px solid #ffffff;
      a:hover {
        border-bottom: none;
      }
    }
  }
}
